"use client";

// React and Next
import { useEffect, useRef, useState } from "react";
import Link from "next/link";

// API
import { BuyerSiginOtp, BuyerSignup } from "@/api/clientsideaction/buyerapi";

// Utils
import { GetBussinessName } from "@/utilities/localStorage";
import {
  EmailValidator,
  NameValidator,
  lowerPresent,
  numPresent,
  specialPresent,
  upperPresent,
} from "@/utilities/regex";

// 3rd Party
import { useRouter } from "nextjs-toploader/app";

// Components
import { useTranslation } from "react-i18next";

export default function SignupForm() {
  const [email, setEmail] = useState("");
  const [otp, setOtp] = useState("");
  const [lastName, setLastName] = useState("");
  const [firstName, setFnameName] = useState("");
  const [password, setPassWord] = useState("");
  const [mailError, setMailError] = useState("");
  const [passerror, setPassError] = useState("");
  const [otperror, setOtpError] = useState("");
  const [passwordErrors, setPasswordErrors] = useState([]);
  const [otpsend, setOtpsend] = useState(false);
  const [checkbox, setCheckbox] = useState(false);
  const [overAllcheck, setOverAllcheck] = useState(false);
  const [passwordshow, setPasswordShow] = useState(false);
  const [loader, setLoader] = useState(false);
  const router = useRouter();
  const [emaileEdit, setEmailEdit] = useState(false);
  const [otpEdit, setOtpEdit] = useState(false);
  const initialref = useRef(null);
  const [otpLoader, setOtpLoader] = useState(false);
  const { t } = useTranslation();
  let bussiness = GetBussinessName();

  const [emailErrorShow, setEmailErrorShow] = useState(false);
  const [passwordErrorShow, setPasswordErrorShow] = useState(false);
  const [OTPErrorShow, setOTPErrorShow] = useState(false);

  useEffect(() => {
    if (validate() && emaileEdit == false && otpEdit == false) {
      setOverAllcheck(true);
    } else {
      setOverAllcheck(false);
    }
  }, [
    email,
    otp,
    lastName,
    password,
    firstName,
    checkbox,
    emaileEdit,
    otpEdit,
  ]);

  const handleAccountcreate = () => {
    if (validate()) {
      setLoader(true);
      BuyerSignup(
        email,
        otp,
        lastName,
        firstName,
        password,
        router,
        setLoader,
        setOtpError,
        setOtpEdit
      );
    }
  };
  const colourStylesError = {
    control: (styles, state) => ({
      ...styles,
      cursor: "pointer",
      borderRadius: "0.25rem",
      height: "2.75rem",
      boxShadow: state.isFocused ? 0 : 0,
    }),
    option: (base, state) => ({
      ...base,
      cursor: "pointer",
      backgroundColor: state.isSelected ? "#ffffff" : "#ffffff",

      "&:hover": { backgroundColor: "#F9F9FC" },

      fontSize: ".8125rem",
      padding: "8px",
      color: "#333333",
    }),
  };

  const validate = () => {
    let ValidateObj = {
      email: false,
      password: true,
      fullname: true,
      otpval: false,
      lastname: true,
      checkval: true,
    };
    if (emaileEdit == false) {
      if (email == "") {
        setMailError(t("validationMessage.emailEmpty"));
        ValidateObj.email = false;
      } else {
        if (EmailValidator(email)) {
          setMailError("");
          ValidateObj.email = true;
        } else {
          setMailError(t("validationMessage.incorrectEmail"));
          ValidateObj.email = false;
        }
      }
    }
    if (password == "") {
      setPassError(t("validationMessage.passwordEmpty"));
      ValidateObj.password = false;
    } else {
      let arrayValue = [];
      if (!upperPresent(password)) {
        arrayValue.push("Must contain at least 1 in Capital Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 in Capital Case"
        );
      }

      if (!numPresent(password)) {
        arrayValue.push("Must have at least 1 Number");
      } else {
        arrayValue.filter((data) => data == "Must have at least 1 Number");
      }
      if (!lowerPresent(password)) {
        arrayValue.push("Must contain at least 1 Lower Case");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Lower Case"
        );
      }
      if (!specialPresent(password)) {
        arrayValue.push("Must contain at least 1 Special characters");
      } else {
        arrayValue.filter(
          (data) => data == "Must contain at least 1 Special characters"
        );
      }
      if (password.length <= 5) {
        arrayValue.push("Must be at least 6 characters");
      } else {
        arrayValue.filter((data) => data == "Must be at least 6 characters");
      }
      setPasswordErrors(arrayValue);
      if (arrayValue.length == 0) {
        setPassError("");
        ValidateObj.password = true;
      } else {
        setPassError(
          "Incorrect Password. Please check the password you have entered."
        );
        ValidateObj.password = false;
      }
    }
    if (firstName == "") {
      ValidateObj.fullname = false;
    } else {
      ValidateObj.fullname = true;
    }
    if (checkbox == false) {
      ValidateObj.checkval = false;
    } else {
      ValidateObj.checkval = true;
    }

    if (lastName == "") {
      ValidateObj.lastname = false;
    } else {
      ValidateObj.lastname = true;
    }
    if (otpEdit == false) {
      if (otp.length == 6) {
        setOtpError("");
        ValidateObj.otpval = true;
      } else {
        setOtpError(t("validationMessage.otpLength"));
        ValidateObj.otpval = false;
      }
    }

    if (
      ValidateObj.email == true &&
      ValidateObj.password == true &&
      ValidateObj.fullname == true &&
      ValidateObj.otpval == true &&
      ValidateObj.lastname == true &&
      ValidateObj.checkval == true
    ) {
      return true;
    } else {
      return false;
    }
  };
  useEffect(() => {
    setEmailEdit(false);
  }, [email]);

  useEffect(() => {
    setOtpEdit(false);
  }, [otp]);

  const handleRoute = () => {
    router.push("/buyer-login");
  };

  const handleOtp = () => {
    if (email !== "" && mailError == "") {
      setOtpLoader(true);
      BuyerSiginOtp(
        email,
        setOtpsend,
        setMailError,
        setEmailEdit,
        setOtpLoader,
        handleRoute,
        t
      );
    }
  };
  const handleOtpvalue = (e) => {
    if (e.length <= 6) {
      setOtp(e);
    }
  };
  const handleSubmit = (e) => {
    if (e) {
      e.preventDefault();
    }
  };

  const handleFocusFunction = (type) => {
    if (type == "email") {
      setEmailErrorShow(false);
    } else if (type == "otp") {
      setOTPErrorShow(false);
    } else if (type == "password") {
      setPasswordErrorShow(false);
    }
  };

  const handleBlurFunction = (type) => {
    if (type == "email") {
      setEmailErrorShow(true);
    } else if (type == "otp") {
      setOTPErrorShow(true);
    } else if (type == "password") {
      // setTimeout(() => {
      setPasswordErrorShow(true);
      // }, 1000)
    }
  };

  const handlePasswordChange = (e) => {
    setPassWord(e.target.value.trimStart());
    if (passwordErrors.length != 0) {
      setPasswordErrorShow(true);
    }
  };

  const handleEnterEmailClick = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleOtp();
    }
  };

  const handleRegiesterEnterClick = (e) => {
    if (e.key === "Enter") {
      e.preventDefault();
      handleAccountcreate();
    }
  };

  const handleNameChange = (type, value) => {
    if (value.length <= 32) {
      if (NameValidator(value)) {
        if (type == "fname") {
          setFnameName(value);
        } else {
          setLastName(value);
        }
      }
    }
  };
  return (
    <div className="w-full px-4 py-4 bg-blue-100 xl:px-32 sm:py-20 md:w-1/2">
      <div className="mb-10">
        <h4 className="mb-1 text-2xl font-black text-black-300">
          {t("signin.OnlinePur")}
          {bussiness}
        </h4>

        <p className="text-base font-normal text-black-300">
          {t("signin.TrustSup")}
        </p>
      </div>

      <div className="flex flex-col mb-8 space-y-6">
        <div className="flex space-x-2 rtl:space-x-[unset]">
          <div
            className="relative w-full mb-3 rtl:ml-2"
            data-twe-input-wrapper-init
          >
            <input
              type="text"
              value={email}
              ref={initialref}
              onChange={(e) => setEmail(e.target.value.trimStart())}
              onBlur={(e) => handleBlurFunction("email")}
              onKeyDown={(e) => handleEnterEmailClick(e)}
              onFocus={(e) => handleFocusFunction("email")}
              className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400 ${
                mailError == "" && email !== ""
                  ? "border-sucess"
                  : email !== "" &&
                    emailErrorShow &&
                    mailError &&
                    "border-valid"
              }`}
              id="exampleFormControlInput1"
              placeholder={t("signin.Examplelabel")}
            />

            <label
              htmlFor="exampleFormControlInput1"
              className={
                email == ""
                  ? `pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100`
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                      mailError == "" && email !== ""
                        ? "text-sucess"
                        : email !== "" &&
                          emailErrorShow &&
                          mailError &&
                          "text-red-invalid"
                    }`
              }
            >
              {t("signin.EmailId")}
            </label>

            {mailError && email !== "" && emailErrorShow && (
              <p className="mt-1 text-xs font-normal text-red-invalid">
                {mailError}
              </p>
            )}
          </div>
          <button
            disabled={email !== "" && mailError == "" ? false : true}
            onClick={handleOtp}
            className={`text-white font-bold text-sm w-28 hover:bg-zinc-800 btn justify-center items-center flex bg-boldblack h-11 rounded  ${
              email !== "" && mailError == "" ? "opacity-100" : "opacity-15"
            }`}
          >
            {otpsend ? (
              otpLoader ? (
                <svg
                  aria-hidden="true"
                  role="status"
                  className="inline w-5 h-5 text-white me-3 animate-spin"
                  viewBox="0 0 100 101"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                    fill="#E5E7EB"
                  />
                  <path
                    d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                    fill="currentColor"
                  />
                </svg>
              ) : (
                t("signin.ResendOTP")
              )
            ) : otpLoader ? (
              <svg
                aria-hidden="true"
                role="status"
                className="inline w-5 h-5 text-white me-3 animate-spin"
                viewBox="0 0 100 101"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                  fill="#E5E7EB"
                />
                <path
                  d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                  fill="currentColor"
                />
              </svg>
            ) : (
              t("signin.SendOTP")
            )}
          </button>{" "}
        </div>

        <div className="relative w-full mb-3" data-twe-input-wrapper-init>
          <input
            type="number"
            value={otp}
            maxLength={6}
            onChange={(e) => handleOtpvalue(e.target.value.trimStart())}
            onBlur={(e) => handleBlurFunction("otp")}
            onFocus={(e) => handleFocusFunction("otp")}
            onKeyDown={(e) => handleRegiesterEnterClick(e)}
            onWheel={(e) => e.target.blur()}
            className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400
              ${
                otp.length == 6 && otperror == ""
                  ? "border-sucess"
                  : otp !== "" &&
                    otp.length !== 6 &&
                    OTPErrorShow &&
                    "border-valid"
              }`}
            id="exampleFormControlInput2"
            placeholder={t("signin.Examplelabel")}
          />

          <label
            htmlFor="exampleFormControlInput2"
            className={
              otp == ""
                ? "pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100"
                : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                    otp.length == 6 && otperror == ""
                      ? "text-sucess"
                      : otp !== "" &&
                        otp.length !== 6 &&
                        OTPErrorShow &&
                        "text-red-invalid"
                  }`
            }
          >
            {t("signin.EnterOtp")}
          </label>

          {otperror && otp !== "" && OTPErrorShow && (
            <p className="mt-1 text-xs font-normal text-red-invalid">
              {otperror}
            </p>
          )}
        </div>

        <div className="relative w-full mb-3" data-twe-input-wrapper-init>
          <input
            type="text"
            value={firstName}
            onChange={(e) =>
              handleNameChange("fname", e.target.value.trimStart())
            }
            onBlur={(e) => handleBlurFunction("fname")}
            onFocus={(e) => handleFocusFunction("fname")}
            onKeyDown={(e) => handleRegiesterEnterClick(e)}
            className={
              firstName == ""
                ? "peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400"
                : `peer block h-11 w-full rounded border border-sucess bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:shadow-none focus:ring-transparent focus:border-sucess ${
                    firstName !== "" && "border-sucess"
                  }`
            }
            id="exampleFormControlInput3"
            placeholder={t("signin.Examplelabel")}
          />

          <label
            htmlFor="exampleFormControlInput3"
            className={
              firstName == ""
                ? "pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100"
                : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-sucess transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                    firstName !== "" && "text-sucess"
                  }`
            }
          >
            {t("signin.FirstName")}
          </label>
        </div>

        <div className="relative w-full mb-3" data-twe-input-wrapper-init>
          <input
            type="text"
            value={lastName}
            onChange={(e) =>
              handleNameChange("lname", e.target.value.trimStart())
            }
            onBlur={(e) => handleBlurFunction("lname")}
            onFocus={(e) => handleFocusFunction("lname")}
            onKeyDown={(e) => handleRegiesterEnterClick(e)}
            className={`peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400
              ${lastName !== "" && "border-sucess"}`}
            id="exampleFormControlInput4"
            placeholder={t("signin.Examplelabel")}
          />

          <label
            htmlFor="exampleFormControlInput4"
            className={
              lastName == ""
                ? "pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100"
                : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 ${
                    lastName !== "" && "text-sucess"
                  }`
            }
          >
            {t("signin.LastName")}
          </label>
        </div>

        <div className="relative w-full mb-3" data-twe-input-wrapper-init>
          <form autoComplete="off" onSubmit={handleSubmit}>
            <input
              type={passwordshow ? "text" : "password"}
              value={password}
              onChange={(e) => handlePasswordChange(e)}
              onBlur={(e) => handleBlurFunction("password")}
              onFocus={(e) => handleFocusFunction("password")}
              onKeyDown={(e) => handleRegiesterEnterClick(e)}
              className={
                password == ""
                  ? "pe-[50px] peer block h-11 w-full rounded border border-grey-400 bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent focus:border-grey-400"
                  : `pe-[50px] peer block h-11 w-full rounded border border-grey-400  bg-transparent px-3 py-[0.32rem] leading-[1.6] outline-none transition-all duration-200 ease-linear focus:placeholder:opacity-100 text-black peer-focus:text-primary data-[twe-input-state-active]:placeholder:opacity-100 motion-reduce:transition-none dark:text-white dark:placeholder:text-grey-400 dark:autofill:shadow-autofill dark:peer-focus:text-primary [&:not([data-twe-input-placeholder-active])]:placeholder:opacity-0 focus:border-grey-400 focus:shadow-none focus:ring-transparent 
                    ${
                      passerror == "" && password !== ""
                        ? "border-sucess"
                        : password !== "" &&
                          passwordErrorShow == true &&
                          passerror &&
                          "border-valid"
                    } `
              }
              id="exampleFormControlInput5"
              placeholder={t("signin.Examplelabel")}
            />

            <label
              htmlFor="exampleFormControlInput5"
              className={
                password == ""
                  ? "pointer-events-none absolute left-3 top-2  mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[1.01rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.8rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100"
                  : `translate-y-[-0.8rem] scale-[0.8] pointer-events-none absolute left-3 top-1 mb-0 max-w-[90%] origin-[0_0] truncate leading-[1.6] text-neutral-500 transition-all duration-200 ease-out peer-focus:-translate-y-[0.8rem] peer-focus:scale-[0.8] peer-focus:text-primary peer-data-[twe-input-state-active]:-translate-y-[0.9rem] peer-data-[twe-input-state-active]:scale-[0.8] motion-reduce:transition-none dark:text-neutral-400 dark:peer-focus:text-primary bg-blue-100 text-red-valid ${
                      passerror == "" && password !== ""
                        ? "text-sucess"
                        : password !== "" &&
                          passwordErrorShow &&
                          passerror &&
                          "text-red-invalid"
                    }`
              }
            >
              {t("signin.EnterPassword")}
            </label>
          </form>

          {password !== "" && passwordErrorShow && (
            <div className="mt-2">
              <ul className="grid grid-cols-1 list-disc gap-y-2 sm:grid-cols-2 ps-7">
                <li
                  className={`text-xs font-normal ${
                    passwordErrors.includes(
                      "Must contain at least 1 in Capital Case"
                    )
                      ? "text-grey-400"
                      : "text-activegreen"
                  }`}
                >
                  {t("validationMessage.CapitalCase")}
                </li>

                <li
                  className={`text-xs font-normal ${
                    passwordErrors.includes("Must have at least 1 Number")
                      ? "text-grey-400"
                      : "text-activegreen"
                  }`}
                >
                  {t("validationMessage.Number")}
                </li>

                <li
                  className={`text-xs font-normal ${
                    passwordErrors.includes(
                      "Must contain at least 1 Lower Case"
                    )
                      ? "text-grey-400"
                      : "text-activegreen"
                  }`}
                >
                  {t("validationMessage.LowerCase")}
                </li>

                <li
                  className={`text-xs font-normal ${
                    passwordErrors.includes("Must be at least 6 characters")
                      ? "text-grey-400"
                      : "text-activegreen"
                  }`}
                >
                  {t("validationMessage.Characters")}
                </li>

                <li
                  className={`text-xs font-normal ${
                    passwordErrors.includes(
                      "Must contain at least 1 Special characters"
                    )
                      ? "text-grey-400"
                      : "text-activegreen"
                  }`}
                >
                  {t("validationMessage.SpecChars")}
                </li>
              </ul>
            </div>
          )}

          <a
            role="button"
            className="absolute flex items-center space-x-3 text-sm font-medium cursor-pointer top-3 right-3 text-black-300"
            onClick={() => setPasswordShow(!passwordshow)}
          >
            {passwordshow ? t("signin.Hide") : t("signin.Show")}
          </a>
        </div>
      </div>

      <div className="flex items-center mb-8 space-x-2">
        <input
          name="comments"
          type="checkbox"
          checked={checkbox}
          onChange={() => setCheckbox(!checkbox)}
          onKeyDown={(e) => handleRegiesterEnterClick(e)}
          className="w-4 h-4 text-black border-gray-300 rounded cursor-pointer focus:ring-0 focus:ring-transparent"
        />

        <p className="text-sm font-normal text-black-300">
          {t("signin.IAccept")}{" "}
          <a
            href={"/terms-conditions"}
            target="_blank"
            className="text-sm font-normal cursor-pointer text-black-300 hover:underline"
          >
            {t("signin.TandC")}
          </a>
        </p>
      </div>

      <div>
        <button
          onClick={handleAccountcreate}
          className={`w-full text-white font-bold text-base hover:bg-zinc-800s bg-boldblack justify-center items-center flex bg-boldblack h-11 rounded  ${
            overAllcheck == true ? "opacity-100" : "opacity-15 cursor-auto"
          }`}
        >
          {loader ? (
            <svg
              aria-hidden="true"
              role="status"
              className="inline w-5 h-5 text-white me-3 animate-spin"
              viewBox="0 0 100 101"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                fill="#E5E7EB"
              />
              <path
                d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                fill="currentColor"
              />
            </svg>
          ) : (
            t("signin.CreateAccount")
          )}
        </button>
      </div>

      <div className="flex justify-center mt-4">
        <p className="group flex space-x-1 rtl:space-x-[unset] font-normal text-black-300 text-sm">
          <span className="rtl:ml-1">{t("signin.AlreadyAccount")}</span>
          <Link
            prefetch={false}
            className="group-hover:text-blue-500"
            href="/buyer-login"
          >
            {" "}
            {t("signin.SignIn")}
          </Link>
        </p>
      </div>
    </div>
  );
}
